import React, {Component} from 'react';
import DropedElement from "./DropedElement";


class DropedList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            opened: true
        }
    }

    openClose = () => this.setState({opened: !this.state.opened})

    createList = (list) => Object.keys(list).map((key, i)=><DropedElement name={key} value={list[key]}/>);


    render() {
        return (
            <div className={'dropedList'}>
                {this.createList(this.props)}
            </div>
        );
    }
}

export default DropedList;