import React, {Component} from 'react';
import DropedList from './DropedList';

class DropedElement extends Component {

    state = {
        opened: true
    };

    isObject = ( value ) => typeof value === 'object';

    insertParam = () => this.isObject(this.props.value) ? <DropedList {...this.props.value}/> : <div>{this.props.value}</div>

    openClose = () => this.setState({opened: !this.state.opened})

    createIndicator = () => this.isObject(this.props.value) ? this.state.opened ? String.fromCharCode(9650) : String.fromCharCode(9660 ) : null

    render() {
        return (
            <div className={'element'} >
                <div className={'indicator'} onClick={this.openClose}>{this.createIndicator()}</div>
                <div className={'name'}>{this.props.name}:</div>
                <div className={'value'}>{this.state.opened ? this.insertParam() : "{...}"}</div>
            </div>
        );
    }
}

export default DropedElement;